import { Stack, TextField } from '@mui/material'
import { ThemeApi } from 'API/CMS/theme'
import DialogConfirm from 'components/DialogConfirm'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAppSelector } from 'redux/hooks'

export default function Create() {
	const nav = useNavigate()
	const [loading, setLoading] = useState(false)
	const [name, setName] = useState('')
	const list = useAppSelector((s) => s.CMS.Theme.list)
	const [createdId, setCreatedId] = useState(0)
	useEffect(() => {
		if (list.find((f) => f.id === createdId)) {
			nav(`/cms/theme/${createdId}`)
		}
	}, [createdId, list, nav])

	return (
		<DialogConfirm
			open
			loading={loading}
			handleClose={() => nav(-1)}
			title="テーマ作成"
			primaryButtonText="作成"
			handleSubmit={async () => {
				try {
					setLoading(true)
					const { id } = await ThemeApi.create({ name })
					setCreatedId(id)
				} finally {
					setLoading(false)
				}
			}}
		>
			<Stack>
				<TextField
					variant="outlined"
					size="small"
					sx={{
						flex: 1,
					}}
					autoFocus
					required
					label={'テーマ名'}
					placeholder="例：トップページ用"
					InputLabelProps={{ shrink: true }}
					value={name}
					onChange={(e) => setName(e.target.value)}
				/>
			</Stack>
		</DialogConfirm>
	)
}
